import React from 'react';
import { createGlobalStyle, ThemeProvider } from 'styled-components';
import { BrowserRouter } from 'react-router-dom';
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';

import theme from '../tools/theme';
import reducers from '../reducers/all-reducers';

const store = createStore(reducers, applyMiddleware(thunk));

const GlobalStyle = createGlobalStyle`
    html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none;&::after,&::before{content:'';content:none}}table{border-collapse:collapse;border-spacing:0}*,*::after,*::before{box-sizing:border-box}

    html {
        font-size: 16px;
    }

    body {
        font: 1em / 1.4em ${({ theme}) => theme.fontFamily.primary };
        background-color: ${({ theme }) => theme.bg.primary };
        color: ${({ theme }) => theme.color.white };
        overflow: hidden;
    }
`;

const Layout = ({ children }) => (
    <Provider store={store}>
        <BrowserRouter>
            <ThemeProvider theme={theme}>
                <>
                    <GlobalStyle />
                    { children }
                </>
            </ThemeProvider>
        </BrowserRouter>
    </Provider>
    
);

export default Layout;